import { mapMutations } from "vuex"
export default {
	name: '',
	data() {
		return {
			actionList: [
				{
					text: '编辑个人资料',
					navUrl: 'edit'
				},
				{
					text: '联系客服',
					navUrl: 'content'
				},
				{
					text: '吐个槽',
					navUrl: 'opinion'
				},
				{
					text: '关于我们',
					navUrl: 'about'
				}
			]
		}
	},
	methods: {
		...mapMutations(['outApp']),
		actionTapHandle(navUrl) {
			switch(navUrl) {
				case 'edit':
					uni.navigateTo({
						url: '../initial/personaldata?type=edit',
					});
					break;
				case 'content':
					break;
				case 'opinion':
					break;
				case 'about':
					break;
				case 'outApp':
					this.outApp();
					uni.reLaunch({
						url: '../initial/wxoauth'
					});
					break;
			}
		}
	},
}
